import React from 'react'
import { Image, SafeAreaView, ScrollView, StyleSheet, View } from 'react-native'
import { Text } from 'react-native-elements'
import * as FileSystem from 'expo-file-system';
import Layout from '../constants/Layout'

const PHOTOS_DIR = FileSystem.documentDirectory + 'photos';

class HistoryScreen extends React.Component {

  state = {
    photos: [],
  };

  componentDidMount = async () => {
    const photos = await FileSystem.readDirectoryAsync(PHOTOS_DIR);
    this.setState({photos});
  };

  render() {
    return (
      <SafeAreaView>
        <ScrollView>
          <Text h2 h2Style={styles.h2Style}>
            History
          </Text>
          <Text h4 h4Style={styles.h4Style}>
            See all pictures and their corresponding top prediction
          </Text>
          <View style={styles.grid}>
            {this.state.photos.map((fileName) => (<View key={fileName}>
              <Image
                style={{width: Layout.window.width, height: Layout.window.height / 3}}
                source={{uri: `${PHOTOS_DIR}/${fileName}`}}
                key={fileName}
              />
            </View>))}
          </View>
        </ScrollView>
      </SafeAreaView>
    )
  }
}

const styles = StyleSheet.create({
  h2Style: {
    fontWeight: 'bold',
    textAlign: 'center',
    color: '#000000',
  },
  h4Style: {
    textAlign: 'center',
    color: '#757575',
  },
  grid: {
    marginTop: 20,
    marginBottom: 20,
  }
});

export default HistoryScreen
